<template>
  <div class="row m-4">
    <div class="col-lg-12 p-0 card">
      <div class="modal" tabindex="-1" role="dialog">
        <div class="modal-dialog" role="document">
          <div class="modal-content">
            <div class="modal-header">
              <h5 class="modal-title">Modal title</h5>
              <button type="button" class="close" data-dismiss="modal" aria-label="Close">
                <span aria-hidden="true">&times;</span>
              </button>
            </div>
            <div class="modal-body">
              <p>Modal body text goes here.</p>
            </div>
            <div class="modal-footer">
              <button type="button" class="btn btn-primary">Save changes</button>
              <button type="button" class="btn btn-secondary" data-dismiss="modal">Close</button>
            </div>
          </div>
        </div>
      </div>

      <div class="card-header">Yeni Yazı Ekle</div>
      <form class="card-body" onsubmit="return false;">
        <div class="form-group">
          <label>Başlık</label>
          <input type="text" class="form-control" placeholder="Başlık" v-model="Baslik" />
        </div>
        <div class="form-group">
          <label>Özet</label>
          <textarea rows="5" class="form-control" placeholder="Özet" v-model="Ozet" />
        </div>
        <div class="form-group">
          <label>İçerik</label>
          <editor
            apiKey="5vodwm3bmkeuphl33gdk756mvcqba5lxqarzyr5tcrw48jl5"
            v-model="Icerik"
            :init="{ entity_encoding : 'raw' }"
          />
        </div>
        <button class="btn btn-danger px-5 py-2" @click="Ekle">Kaydet</button>
      </form>
    </div>
  </div>
</template>

<script>
import Editor from "@tinymce/tinymce-vue";

function modalGoster() {
  $(".modal").modal("show");
}

export default {
  name: "BlogEkle",
  components: {
    editor: Editor
  },
  data() {
    return {
      Baslik: "",
      Ozet: "",
      Icerik: "",
      Hata: ""
    };
  },
  methods: {
    Ekle() {
      fetch(process.env.VUE_APP_SERVER_URL, {
        method: "POST",
        headers: {
          "Content-Type": "application/json",
          token: this.$session.get("token")
        },
        body: JSON.stringify({
          Baslik: this.Baslik,
          Ozet: this.Ozet,
          Icerik: this.Icerik
        })
      })
        .then(res => {
          if (res.status == 201) this.$router.push({ name: "Admin-Bloglar" });
          else return res.json();
        })
        .then(res => {
          let result = Object.entries(res.errors);

          for (let i = 0; i < result.length; i++) {
            for (let z = 0; z < result[i].length; z++) {
              result[i][z].message
                ? (this.hata += result[i][z].message + "\n")
                : null;
            }
          }

          modalGoster();
        });
    }
  }
};
</script>

<style>
.tox-tinymce {
  height: 75vh !important;
}
</style>